.new-todo__controls {
    display: inline-block;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
}

.new-todo__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
}

.new-todo__control input {
    font: inherit;
    margin-bottom: 1rem;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
}

.new-todo__control select {
    font: inherit;
    margin-bottom: 1rem;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 21.25rem;
    max-width: 100%;
}

.new-todo__actions {
    text-align: center;
}